import { ToggleControl } from "@wordpress/components";
import { useState } from "@wordpress/element";
import { useAttributes } from "../../context/AttributesContext";

export default function BoxShadow({ label }) {
	const { attributes, setAttributes } = useAttributes();
	const { boxShadow } = attributes.container;
	const [value, setValue] = useState(boxShadow === "none" ? false : true);

	const onToggleShadow = (value) => {
		if (value) {
			setAttributes({
				container: {
					...attributes.container,
					boxShadow: "0 0 20px rgba(102, 126, 234, 0.6)",
				},
			});
		} else {
			setAttributes({
				container: {
					...attributes.container,
					boxShadow: "none",
				},
			});
		}
		setValue(value);
	};
	return (
		<div className="components-header">
			<p>{label} </p>
			<span>
				<ToggleControl checked={value} onChange={onToggleShadow} />
			</span>
		</div>
	);
}
